import React from 'react'
import './styles.css'

import Usercard from './Usercard'
import Step from './Step'

export default ({ match }) =>
  <div className="bContent">
    <h1>Текущие сделки </h1>
    <div className="innerContent">
      <p className="bDescription">Текущие сделки — наглядная воронка продаж которая поможет понять, на каком этапе находятся все ваши клиенты.</p>
      <p className="bDescription">Эта функция ещё тестируется и дорабатывается. Мы сообщим вам, когда она будет готова.</p>
      <div className="bCustomersBoard">
        <Step title="Звонки">
          <Usercard title="Замечательный тапир" details="Test customer description" />
          <Usercard title="Утончённый ягуар" details="Test customer description" />
          <Usercard title="Чудная акула" details="Test customer description" />
          <Usercard title="Стильная белка" details="Test customer description" />
          <Usercard title="Шикарный кот" details="Test customer description" />
        </Step>

        <Step title="Встреча">
          <Usercard title="Энергичная лягушка" details="Test customer description" />
          <Usercard title="Обворожительная капибара" details="Test customer description" />
          <Usercard title="Мечтательный муравьед" details="Test customer description" />
          <Usercard title="Бесподобная зебра" details="Test customer description" />
        </Step>

        <Step title="Ценовое">
          <Usercard title="Жизнерадостный фламинго" details="Test customer description" />
          <Usercard title="Дружелюбный варан" details="Test customer description" />
        </Step>

        <Step title="Договор">
          <Usercard title="Энергичная улитка" details="Test customer description" />
        </Step>
      </div>
    </div>
  </div>;



